import React from "react"
import './styles/App.css';
import {BrowserRouter as Router , Routes,Route,Navigate} from "react-router-dom"
//import Login from "./pages/Login"
import * as PATHS from './constants/Routes'
import UserContext from "./contexts/UserContext";
import useAuthUserListener from "./hooks/useAthUserListener"
import SharedLayout from './components/SharedLayout'
//import FirebaseContext from './contexts/firebase'

const Login = React.lazy(() => import('./pages/Login'));
const Dashboard = React.lazy(() => import('./pages/Dashboard'));
const NotAvailable = React.lazy(() => import('./pages/NotAvailable'));

function App() {
  const user = useAuthUserListener();
  //const {auth} = React.useContext(FirebaseContext);
  //const currUser = auth.currentUser;
  
  return (<div>
          <UserContext.Provider value={user}>
            <Router>
              <React.Suspense fallback={<h3>Loading</h3>} >
                <Routes>
                  {
                  user?
                  <>
                    <Route path="/transaction" element={<SharedLayout />}>
                      <Route path = {"/transaction"+PATHS.HOME} index element ={<Dashboard />}/> 
                      <Route path="*" element ={<NotAvailable/>}/>
                    </Route>
                    
                    <Route path={'/transaction'+PATHS.LOGIN} element={<Navigate replace to={'/transaction'+PATHS.HOME} />} />
                  </>
                  : 
                  <>
                    <Route path={'/transaction'+PATHS.LOGIN} element={<Login/>} />
                    <Route path={'/transaction'+PATHS.HOME} element={<Navigate replace to={'/transaction'+PATHS.LOGIN} />} />
                    <Route path="/transaction" element={<SharedLayout />}>
                      <Route path="*" element ={<NotAvailable/>}/>
                    </Route>
                  </>
                  }

                </Routes>
              </React.Suspense>
            </Router>
            
          </UserContext.Provider>
          </div>
    
  );
}

export default App;
